import { Link } from "gatsby";
import React from "react";
import '../styles/footer.css';

export default function Footer() {
    return (
        <div className="container-fluid mt-5 pt-5 footer">
            <div className="row">
                <div className="col-12 col-lg-2 align-center text-center">
                    <Link to="#"><img src="/fort/images/fort-footer.png" alt="" /></Link>
                </div>
                <div className="col-12 col-lg-5 my-5">
                    <ul className="menu-footer">
                        <li><Link to="#">Instituicional</Link></li>
                        <li><Link to="#">Trabalhe Conosco</Link></li>
                        <li><Link to="#">Novidades</Link></li>
                        <li><Link to="#">Nossas Lojas</Link></li>
                        <li><Link to="#">Ofertas</Link></li>
                        <li><Link to="#">Delivery</Link></li>
                        <li><Link to="#">Poltica de Privacidade</Link></li>
                    </ul>
                </div>
                <div className="col-12 col-lg-5 footer-media">
                    <div>
                        <h6>Uma empresa</h6>
                        <Link to="#"><img className="pereira-img" src="/fort/images/grupo-pereira.png" alt="" /></Link>
                    </div>
                    <div className="text-center">
                        <h6>Nossas redes sociais</h6>
                        <div className="images">
                            <Link to="#"><img src="/fort/images/facebook.png" alt="" /></Link>
                            <Link to="#"><img src="/fort/images/youtube.png" alt="" /></Link>
                            <Link to="#"><img src="/fort/images/instagram.png" alt="" /></Link>
                            <Link to="#"><img src="/fort/images/linkedin.png" alt="" /></Link>
                        </div>
                    </div>
                    <div className="text-center teste">
                        <img className="cell-fort-img" src="/fort/images/cell-fort.png" alt="" />
                    </div>
                </div>
            </div>
        </div>
    );
}
