import React from "react";
import '../styles/conheca.css';
import { useInView } from 'react-intersection-observer';

export default function Conheca() {
    const [conhecaRef, conhecaInView] = useInView();    
    const [acougueRef, acougueInView] = useInView();    
    const [frutaRef, frutaInView] = useInView();    
    const [vuonRef, vuonInView] = useInView();    
    const [vendaRef, vendaInView] = useInView();    

    return (
        <div className="container-lg">
            <div ref={conhecaRef} className="row conheca-title">
                <div className={conhecaInView ? 'col from-left text-center show' : "col from-left text-center"}>
                    <h1>Conhea o que o Fort <span className="red">Faz pra voc</span></h1>
                </div>
            </div>
            <div ref={acougueRef} className="row conheca mt-5">
                <div className={acougueInView ? "col-md-6 text-center text-md-start from-left show" : 'col-md-6 from-left'}>
                    <img className="img-fluid" src="/fort/images/acougue.png" alt="" />
                </div>
                <div className={acougueInView ? "col-md-6 text-center text-md-start from-right show" : 'col-md-6 from-right'}>
                    <h1 className="red">Aougue Carne Fresca</h1>
                    <p>Os melhores cortes, sempre fresquinhos. Venha conhecer o melhor do nosso Aougue. Tem dicas, curiosidades, receitas e muito mais.</p>
                    <div className="text-center">
                        <button>VISITAR AOUGUE</button>
                    </div>
                </div>
            </div>
            <div ref={frutaRef} className="row conheca mt-5">
                <div className={frutaInView ? "col-md-6 text-center text-md-start from-left show" : 'col-md-6 from-left'}>
                    <h1 className="red">Frutas, Legumes e Verduras</h1>
                    <p>Parece horta, mas  o nosso FLV. Aqui voc encontra variedade de frutas, legumes e verduras.</p>
                    <div className="text-center">
                        <button>VER HORTIFRUTI</button>
                    </div>
                </div>
                <div className={frutaInView ? "col-md-6 order-first order-md-last text-center text-md-end from-right show" : 'col-md-6 text-end from-right'}>
                    <img className="img-fluid" src="/fort/images/hortifruti.png" alt="" />
                </div>
            </div>
            <div ref={vuonRef} className="row conheca mt-5">
                <div className={vuonInView ? "col-md-6 text-center text-md-start from-left show" : 'col-md-6 from-left'}>
                    <img className="img-fluid" src="/fort/images/vuon.png" alt="" />
                </div>
                <div className={vuonInView ? "col-md-6 text-center text-md-start from-right show" : 'col-md-6 from-right'}>
                    <h1 className="red">Vuon Card</h1>
                    <p>Conhea o Vuon Card, o carto de crdito do Fort. Parcele suas compras, pague a primeira parcela em at 40 dias e tenha controle da sua fatura pelo aplicativo.</p>
                    <div className="text-center">
                        <button>CONHECER O VUON</button>
                    </div>
                </div>
            </div>
            <div ref={vendaRef} className="row conheca mt-5">
                <div className={vendaInView ? "col-md-6 text-center text-md-start from-left show" : 'col-md-6 from-left'}>
                    <h1 className="red">Televendas</h1>
                    <p>Empresria e empresrio, a gente tem as melhores solues do tamanho do seu negcio.  variedade e preo baixo que vocs querem? Ento, seja um Parceiro F.</p>
                    <div className="text-center">
                        <button>SER PARCEIRO</button>
                    </div>
                </div>
                <div className={vendaInView ? "col-md-6 order-first order-md-last text-center text-md-end from-right show" : 'col-md-6 from-right'}>
                    <img className="img-fluid" src="/fort/images/televendas.png" alt="" />
                </div>
            </div>
        </div>
    );
}
