import React, { useState, useEffect } from "react";
import '../styles/marcas.css';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";
import "bootstrap-icons/font/bootstrap-icons.css";
import { useInView } from 'react-intersection-observer';

export default function Marcas() {
    const [slideNumber, setSlideNumber] = useState(5);
    useEffect(() => {
        function handleResize() {
            if (window.innerWidth < 992) setSlideNumber(3)
            else setSlideNumber(5);
            if (window.innerWidth < 768) setSlideNumber(2);            
        }
        window.addEventListener("resize", handleResize);
        return () => window.removeEventListener("resize", handleResize);
    }, []);
    var settings = {
        infinite: true,
        speed: 500,
        slidesToShow: slideNumber,
        slidesToScroll: 1,
        autoplay: true,
        arrows: true
    };
    const [marcasRef, marcasInView] = useInView();
    
    return (
        <div ref={marcasRef} className={marcasInView ? 'container-lg fade-in show-marcas mt-5 pt-5' : 'container-lg fade-in mt-5 pt-5'}>
            <div className="row mb-4">
                <div className="col marcas text-center">
                    <h1>Grandes marcas,</h1>
                    <h1>com produtos de qualidade</h1>
                </div>
            </div>
            <div className="row px-md-0">
                <div className="col px-5 px-lg-4">
                    <Slider {...settings}>
                        <div className="center"><img className="img-fluid" src="/fort/images/bunge.png" alt="" /></div>
                        <div className="center"><img className="img-fluid" src="/fort/images/caboclo.png" alt="" /></div>
                        <div className="center"><img className="img-fluid" src="/fort/images/cargill.png" alt="" /></div>
                        <div className="center"><img className="img-fluid" src="/fort/images/casa-do-queijo.png" alt="" /></div>
                        <div className="center"><img className="img-fluid" src="/fort/images/colgate.png" alt="" /></div>
                        <div className="center"><img className="img-fluid" src="/fort/images/danone.png" alt="" /></div>
                        <div className="center"><img className="img-fluid" src="/fort/images/flora.png" alt="" /></div>
                        <div className="center"><img className="img-fluid" src="/fort/images/frimesa.png" alt="" /></div>
                        <div className="center"><img className="img-fluid" src="/fort/images/garoto.png" alt="" /></div>
                        <div className="center"><img className="img-fluid" src="/fort/images/harald.png" alt="" /></div>
                        <div className="center"><img className="img-fluid" src="/fort/images/itambe.png" alt="" /></div>
                        <div className="center"><img className="img-fluid" src="/fort/images/petropolis.png" alt="" /></div>
                    </Slider>
                </div>
            </div>
            <div className="row text-center mt-5 pt-5">
                <div className="col social-media">
                    <h6>Compartilhar esta pgina</h6>
                    <div>
                        <button><i class="bi bi-facebook"></i></button>
                        <button><i class="bi bi-twitter"></i></button>
                        <button><i class="bi bi-linkedin"></i></button>
                        <button><i class="bi bi-whatsapp"></i></button>
                    </div>
                </div>
            </div>
        </div>
    );
}
